.contact-highlight {
  border-radius: 10px;
  -webkit-animation: blink 1s infinite;
  -moz-animation:    blink 1s infinite;
  -o-animation:      blink 1s infinite;
  animation:         blink 1s infinite;
}

@keyframes blink { 
  0%{ 
    color: #000;
    background-color: #FFF;
  }
  100%{
    color: #FFF;
    background-color: #189a95;
  }
}
